<template>
  <div class="hello">
    <h2>喵多鱼系统原型示意图</h2>
    <div style="height:1080px;width:1920px;border:1px solid black;margin:0 auto;text-align:center">
      <div style="background:#ffca0a;text-align:center;padding:0px 40px;height:180px">
        <img src="../../assets/banner/banner_server.jpg" alt="banner">
      </div>
      <div>
        <div class="left">
          <div class="li-left">订单管理</div>
          <div class="li-left-now">骑手管理</div>
          <div class="li-left">用户管理</div>
          <div class="li-left">门点管理</div>
          <div class="li-left">代理商管理</div>
          <div class="li-left">账号与权限管理</div>
          <div class="li-left">电子协议管理</div>
          <div class="li-left">投诉管理</div>
          <div class="li-left">规则配置</div>
        </div>
        <div class="right">
          <h1 style="margin-left:50px">订单管理</h1>
          <h4 style="margin-left:50px">点击骑手的完成订单数可查看历史订单。点击骑手的支付总额可查看支付清单。</h4>
          <table>
            <tr>
              <th scope="">序号</th>
              <th scope="">姓名</th>
              <th scope="">工号</th>
              <th scope="">性别</th>
              <th scope="">年龄</th>
              <th scope="">完成订单数</th>
              <th scope="">支付总额（元）</th>
              <th scope="">操作</th>
            </tr>
            <tr v-for="(obj , index) in a" :key="obj.work_id">
              <td>{{index+1}}</td>
              <td>{{obj.name}}</td>
              <td>{{obj.work_id}}</td>
              <td>{{obj.sex}}</td>
              <td>{{obj.age}}</td>
              <td><a href="">{{obj.num_order}}</a></td>
              <td><a href="">{{obj.num_money}}</a></td>
              <td>
                <input type="button" value="暂停">
                <input type="button" value="开除">
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
    <h3>made by 2021/6/26, using vue 2.5.2.<a href="#/">返回主页</a></h3>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data: function () {
    return {
      a: [
        {
          'work_id': 'FDS1234',
          'name': '马冬梅',
          'sex': '女',
          'age': 38,
          'num_order': 32,
          'num_money': 1580
        }, {
          'work_id': 'FDS1235',
          'name': '陈坤',
          'sex': '男',
          'age': 28,
          'num_order': 42,
          'num_money': 1184
        }, {
          'work_id': 'FDS1236',
          'name': '李雪',
          'sex': '女',
          'age': 38,
          'num_order': 15,
          'num_money': 440
        }, {
          'work_id': 'FDS1237',
          'name': '王老七',
          'sex': '男',
          'age': 45,
          'num_order': 12,
          'num_money': 364
        }
      ]
    }
  }
}
</script>

<style scoped>
.left{
  width: 15%;
  float: left;
  text-align: right;
  height: 900px;
  background: rgb(255,211,92,0.8);
}
.li-left{
  padding: 20px;
  cursor: pointer;
  text-align: center;
  font-size: 18px;
}
.li-left:hover{
  font-size: 20px;
  background: rgba(148, 145, 1, 0.2);
}
.li-left-now{
  padding: 20px;
  cursor: pointer;
  text-align: center;
  font-size: 20px;
  background: rgba(148, 145, 1, 0.2);}
.right{
  width: 84%;
  float: left;
  text-align: left;
  height: 900px;
}
th{
  background: rgb(255,211,92);
  border: 1px solid black;
  height:25px
}
td{
    border: 1px solid black;
      height:25px
}
table{
  width: 80%;
  margin: 50px;
  border-collapse:collapse;
}
</style>



